'use client'
import React from 'react';
import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

const salesData = [
  { month: 'Jan', revenue: 4000, orders: 240 },
  { month: 'Feb', revenue: 3000, orders: 139 },
  { month: 'Mar', revenue: 5000, orders: 280 },
  { month: 'Apr', revenue: 4780, orders: 350 },
  { month: 'May', revenue: 5890, orders: 390 },
  { month: 'Jun', revenue: 4390, orders: 290 },
  { month: 'Jul', revenue: 4490, orders: 310 },
];

export default function SalesStatsPage () {
  return (
    <div className="p-6">
      <h1 className="text-3xl font-bold mb-6">Sales Statistics</h1>

      {/* KPI Section */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-10">
        <div className="bg-white p-6 shadow rounded text-center">
          <h2 className="text-lg font-semibold text-gray-600">Total Revenue</h2>
          <p className="text-3xl font-bold text-green-600">$25,000</p>
        </div>
        <div className="bg-white p-6 shadow rounded text-center">
          <h2 className="text-lg font-semibold text-gray-600">Total Orders</h2>
          <p className="text-3xl font-bold text-blue-600">1,200</p>
        </div>
        <div className="bg-white p-6 shadow rounded text-center">
          <h2 className="text-lg font-semibold text-gray-600">Avg Order Value</h2>
          <p className="text-3xl font-bold text-purple-600">$50</p>
        </div>
        <div className="bg-white p-6 shadow rounded text-center">
          <h2 className="text-lg font-semibold text-gray-600">Returning Customers</h2>
          <p className="text-3xl font-bold text-yellow-600">30%</p>
        </div>
      </div>

      {/* Sales Chart */}
      <div className="bg-white p-6 shadow rounded mb-10">
        <h2 className="text-xl font-semibold mb-4">Revenue Trends</h2>
        <ResponsiveContainer width="100%" height={300}>
          <LineChart data={salesData} margin={{ top: 20, right: 30, left: 0, bottom: 5 }}>
            <CartesianGrid stroke="#f5f5f5" />
            <XAxis dataKey="month" />
            <YAxis />
            <Tooltip />
            <Line type="monotone" dataKey="revenue" stroke="#82ca9d" strokeWidth={2} />
            <Line type="monotone" dataKey="orders" stroke="#8884d8" strokeWidth={2} />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Recent Sales Table */}
      <div className="bg-white p-6 shadow rounded">
        <h2 className="text-xl font-semibold mb-4">Recent Sales</h2>
        <table className="table-auto w-full text-left border-collapse">
          <thead>
            <tr className="bg-gray-200">
              <th className="px-4 py-2">Order ID</th>
              <th className="px-4 py-2">Customer Name</th>
              <th className="px-4 py-2">Date</th>
              <th className="px-4 py-2">Amount</th>
              <th className="px-4 py-2">Status</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td className="border px-4 py-2">#12345</td>
              <td className="border px-4 py-2">John Doe</td>
              <td className="border px-4 py-2">01/01/2025</td>
              <td className="border px-4 py-2">$250</td>
              <td className="border px-4 py-2 text-green-600">Completed</td>
            </tr>
            <tr>
              <td className="border px-4 py-2">#12346</td>
              <td className="border px-4 py-2">Jane Smith</td>
              <td className="border px-4 py-2">01/02/2025</td>
              <td className="border px-4 py-2">$180</td>
              <td className="border px-4 py-2 text-yellow-600">Pending</td>
            </tr>
            <tr>
              <td className="border px-4 py-2">#12347</td>
              <td className="border px-4 py-2">Mike Johnson</td>
              <td className="border px-4 py-2">01/03/2025</td>
              <td className="border px-4 py-2">$320</td>
              <td className="border px-4 py-2 text-red-600">Cancelled</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};


